import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [

  {
    path: "dashboard",
    loadChildren: () =>
      import("./modules/dashboard/dashboard.module").then(
        (m) => m.DashboardModule
      ),
  },

  {
    path: "supplier-parameters",
    loadChildren: () =>
      import("./modules/suppliers-parameters/suppliers-parameters.module").then(
        (m) => m.SuppliersParametersModule
      ),
  },

  {
    path: "suppliers-maintenance",
    loadChildren: () =>
      import("./modules/suppliers-maintenance/suppliers-maintenance.module").then(
        (m) => m.SuppliersMaintenanceModule
      ),
  },


  {
    path: "supplier-profiles",
    loadChildren: () =>
      import("./modules/supplier-profile-maintenance/supplier-profile-maintenance.module").then(
        (m) => m.SupplierProfileMaintenanceModule
      ),
  },

  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ErpSuppliersmanagementRoutingModule { }
